import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TP_VIDEO_THUMBNAIL } from '../utils/video-thumbnail.constant';
import { TpVideoThumbnailType } from '../utils/video-thumbnail.type';
import { TpVideoThumbnail } from '../video-thumbnail';

const meta: Meta<TpVideoThumbnail & TpVideoThumbnailType> = {
  title: 'components/Core/Video thumbnail',
  argTypes: {
    value: {
      control: { type: 'range', min: 0, max: 100, step: 1 },
      description: 'Define o valor da barra de progresso.',
      table: {
        defaultValue: { summary: 'undefined' },
        type: { summary: 'number' },
      },
    },
    url: {
      control: { type: 'text' },
      description: 'Define a url da img thumbnail do componente.',
      table: {
        defaultValue: {
          summary: '',
        },
        type: {
          summary: 'text',
        },
      },
    },
    size: {
      control: { type: 'select' },
      options: [...TP_VIDEO_THUMBNAIL['sizes']],
      description: 'Define o tamanho do componente.',
      table: {
        defaultValue: { summary: 'undefined' },
        type: { summary: TP_VIDEO_THUMBNAIL['sizes'].join('|') },
      },
    },
  },
  args: {
    value: 50,
    url: 'https://placehold.co/856x524',
  },
};

export default meta;

type Story = StoryObj<TpVideoThumbnail & TpVideoThumbnailType>;

export const Default: Story = {
  render: ({ ...args }) => {
    return html`
      <tp-video-thumbnail
        color=${args.color}
        size=${args.size}
        value=${args.value / 100}
        url=${args.url}
      ></tp-video-thumbnail>
    `;
  },
};
